<template>
  <div>
    <div class="d-flex justify-content-between align-items-center">
      <div style="line-height: 10px">
        <h4>Available</h4>
        <span class="text-muted">Empower Company</span>
      </div>
      <b-button variant="primary" class="rounded-pill"> New Position </b-button>
    </div>
    <b-row>
      <b-col
        md="3"
        v-for="department in departments"
        :key="department.id"
        v-b-modal="'detail_' + department.id"
      >
        <b-card class="mt-2">
          <b-card-body align="center">
            <b-avatar :src="department.icon" size="50"></b-avatar>
            <b-card-text class="mt-2">
              <h4>{{ department.name }}</h4>
              <span>{{ department.users_count }} People Assigned</span>
            </b-card-text>
          </b-card-body>
        </b-card>
      </b-col>
    </b-row>

    <b-modal
      v-for="department in departments"
      :key="department.id"
      :id="'detail_' + department.id"
      :title="department.name"
      size="md"
      hide-footer
    >
      <form @submit.prevent="form_update(department)">
        <b-form-group>
          <b-form-input
            v-model="department.name"
            type="text"
            placeholder="Department Name"
          />
        </b-form-group>
        <div class="text-right mt-1">
          <b-button
            variant="danger"
            class="mr-1"
            size="sm"
            @click="deleteD(department.id)"
          >
            Delete
          </b-button>
          <b-button type="submit" variant="primary" size="sm">
            Update
          </b-button>
        </div>
      </form>
    </b-modal>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      departments: [],
    };
  },
  mounted() {
    this.callApi({
      method: "GET",
      url: "/teams/fetch",
      success: (response) => {
        this.departments = response.result.data;
      },
      error: (error) => {
        console.log(error);
      },
    });
  },
  methods: {
    deleteD(id) {
      this.callApi({
        method: "DELETE",
        url: "/teams/delete/" + this.hashid(id),
        success: (response) => {
          this.departments = this.departments.filter(
            (department) => department.id != id
          );

          this.$bvModal.hide("detail_" + id);
        },
        error: (error) => {
          console.log(error);
        },
      });
    },
  },
};
</script>
